<template>
  <div id="editUserPswd" class="flex-center flex-column">
      <div class="flex-center" style="width:90%;height:150px;margin-bottom:24px;">
        <img src="../../assets/img/icon/logo@3x.png" alt="" style="max-height:100%;"/>
      </div>
      <van-form @submit="_submit_edit">
        <van-field
            v-model="viewCode"
            name="账号"
            label="账号"
            placeholder="账号"
            :rules="[{ required: true, message: '请填写账号' }]"
        />
        <van-field
            v-model="userName"
            name="姓名"
            label="姓名"
            placeholder="姓名"
            :rules="[{ required: true, message: '请填写姓名' }]"
        />
        <van-field
            v-model="userMobile"
            name="手机号"
            label="手机号"
            placeholder="手机号"
            :rules="[{ required: true, message: '请填写手机号' }]"
        />
        <van-field
            v-model="userPsw"
            type="password"
            name="新密码"
            label="新密码"
            placeholder="新密码"
            :rules="[{ required: true, message: '请填写新密码' }]"
        />
        <van-field
            v-model="userPsw2"
            type="password"
            name="重复密码"
            label="重复密码"
            placeholder="重复密码"
            :rules="[{ required: true, message: '请再次填写密码' }]"
        />
        <van-field
            v-model="userIdcard"
            center
            clearable
            label="短信验证码"
            placeholder="短信验证码"
            :rules="[{ required: true, message: '请填写短信验证码' }]"
        >
            <template #button>
                <van-button
                    size="small"
                    :disabled="!canSendSms"
                    @click="_send_sms"
                    native-type="button"
                >{{sendSmsBtnText}}</van-button>
            </template>
        </van-field>
        <div style="margin: 16px;">
            <van-button round block native-type="submit">修改密码</van-button>
        </div>
        <div class="flex-between flex-vertical" style="margin: 16px;">
            <router-link to="/myLogin" style="color:#fff;font-size:12px;">已有账号</router-link>
        </div>
      </van-form>
  </div>
</template>

<script>
import { editPersonalInfo, sendSms } from '@/api';
export default {
    data:()=>({
        viewCode: '',
        userName: '',
        userMobile: '',
        userPsw: '',
        userPsw2: '',
        userIdcard: '',

        sendingSms: false,
        phoneNumberPattern: /^1[3456789]\d{9}$/,
        waitSmsTime: 60,
        countTimer: null,
        sendSmsBtnText: '发送验证码',
    }),
    computed: {
        canSendSms: function(){
            if(
                !this.sendingSms
                && this.phoneNumberPattern.test(this.userMobile)
                && this.userName !== '' 
            ) return true;
            return false;
        }
    },
    destroyed(){
        clearInterval(this.countTimer);
    },
    methods: {
        _send_sms(){
            let { userName, userMobile } = this;
            sendSms({
                phoneNumber: userMobile,
                username: userName
            }).then(res=>{
                if(res.code === 0){
                    this.$toast.success('发送验证码成功！');
                    this.countTimer = setInterval(()=>{
                        if(this.waitSmsTime === 0){
                            clearInterval(this.countTimer);
                            this.sendingSms = false;
                            this.sendSmsBtnText = '发送验证码';
                            this.countTimer = 60;
                        }else{
                            this.sendingSms = true; 
                            this.sendSmsBtnText = --this.waitSmsTime + '秒后可再发送';
                        }
                    },1000)
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        },
        _submit_edit(){

            let { userPsw, userPsw2, userIdcard, userMobile, userName } = this;

            if(userPsw !== userPsw2){
                this.$toast.fail('两次输入密码不一致！');
                return;
            }

            editPersonalInfo({
                userPsw,
                userPsw2,
                userIdcard,
                userMobile,
                userName
            }).then(res=>{
                if(res.code === 0){
                    this.$toast.success('修改登录密码成功！');
                    this.$router.push('/myLogin')
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        }
    }
}
</script>

<style lang="scss">
    #editUserPswd {
        height: 100%;
        background-color: #2ba06a;
        
        .van-cell {
            background-color: initial;
        }
        .van-form {
            width: 90%;
        }
        .van-field__label {
            color: #fff;
        }
        .van-cell::after {
            border-bottom: 1px solid #fff;
        }
        
        input::-webkit-input-placeholder {
        color: #fff;
        }
        input::-moz-input-placeholder {
        color: #fff;
        }
        input::-ms-input-placeholder {
        color: #fff;
        }
    }
</style>